.button {
  border: none;
  font-family: 'Nunito', sans-serif;
  font-size: 16px;
  &_default {
    background-color: var(--background-main);
    color: var(--dark);
    box-shadow: rgba(0, 0, 0, 0.1) 0px 5px 15px;
  }
  &_active {
    color: white;
    background: var(--background-dark);
  }
}
